* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

/* 변수로 색상 지정 */
:root {
  --baseColor: white;
  /* 주조색(배경색) - 약 70% 사용 */
  --dominateColor: black;
  /* 보조색 - 약 20% 사용 */
  --accentColor: rgb(252, 102, 64);
  /* 포인트 컬러 -  */
}

h1 {
  color: var(--accentColor);
  background-color: var(--dominateColor);line-height: 2;
  text-align: center;
}

section {
  margin-top: 2em;
}

h2 {
  text-align: center;
  font-size: 1em;
  height: 2em;
  color: var(--accentColor);
}

article {
  padding: 1em;
}

h3 {
  margin: 1em 0;
}

article ul, article ol {
  padding-left: 2em;
}

.overview {
  padding: 0.5em;
}

.overview h4 {
  height: 2em;
}

.tip {
  padding: 0.5em 1em;
}

h5 {
  font-size: 1em;
}

table {
border-collapse: collapse;
border-spacing: none;
width: 100%;
}

caption {
  text-align: left;
  height: 2em;
}

th, td {
  border: .1em solid var(--dominateColor);
  padding: 0.5em;
}

thead th {
  background-color: #666;
}

tbody th {
  text-align: left;
  background-color: #ddd;
}

.del {
  color: #ddd;
}

.point {
  font-weight: bold;
  color: var(--accentColor);
}
